h1 {
  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: grey;
}

button {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 14px 40px;
  box-shadow: 0 5px 15px 0 rgba(11, 99, 246, 0.7);
  transition: 0.5s;
  font-family: 'Dela Gothic One', cursive;
  font-weight: 700;
  font-size: 72px;
  background-color: #4caf50;
  border: none;
  border-radius: 40px;
  color: aliceblue;
}

button:hover {
  transform: translate(-51%, -60%);
  box-shadow: 0 20px 40px 0 rgba(11, 99, 246, 0.3);
}

@media screen and (max-width: 768px) {
  body {
    width: 100%;
    max-width: 768px;
  }
  div {
    width: calc(100%-40px);
    font-size: 12px;
  }
}
